@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100 ;0,300 ;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-size: 0.96em;
}

.progressTitle {
  font-size: 1.3em;
}

.doneFilter {
  display: flex;
}

.doneComponents {
  display: flex;
  font-size: 12px;
}

.doneCategory {
  font-size: 1.3em;
}

.doneTitle {
  font-size: 1.4em;
}

.ol-ingredients {
  display: flex;
  flex-direction: column;
  line-height: 0.7em;
}

.li-ingredients {
  display: flex;
  flex-direction: row;
  width: 35%;
}

.page-title {
  font-size: 3em;
}

.TitleShare {
  display: flex;
}

.recipe-ingredientes {
  font-size: 1.3em;
}

.meals {
  align-items: center;
  background: #0fa36b;
  color: #464646;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  height: 100vh;
  justify-content: center;
  width: 100%;
}

.finish-recipe {
  border: 1px solid black;
  bottom: 0;
  height: 50px;
  position: fixed;
  width: 100%;
}

form {
  background-color: rgb(94, 248, 157);
  border: 0.3em solid rgb(67, 163, 58);
  display: flex;
  font-family: Roboto , sans-serif;
  height: 100vh;
  justify-content: center;
  margin: auto;
}

.form-group {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 500px;
  padding: 2em;
  width: 90%;
}

.icons {
  display: flex;
  font-size: 3em;
  justify-content: space-between;
  margin-bottom: 2em;
  width: 100%;
}

/* label {
  display: flex;
  flex-direction: column;
  padding-bottom: 1em;
  width: 100%;
} */

.lbl-txt-login {
  display: none;
}

.input-login {
  border: none;
  border-radius: 0.3em;
  box-sizing: border-box;
  font-size: 1.2em;
  height: 5vh;
  padding: 0.3em;
}

.input-login:focus {
  border: 0.2em solid rgb(67, 163, 58);
  font-size: 1.2em;
  outline: none;
  transition: 0.2s;
}

.btn-login {
  align-items: center;
  background-color: rgb(67, 163, 58);
  border: none;
  border-radius: 0.3em;
  box-sizing: border-box;
  color: white;
  display: flex;
  font-size: 1.3em;
  height: 5vh;
  justify-content: center;
  width: 100%;
}

.btn-login:hover {
  cursor: pointer;
}

.btn-login:disabled {
  background-color: grey;
  color: white;
  cursor: not-allowed;
}

.btn-login:enabled {
  background-color: #5ce250;
  color: black;
  transition: 2s;
}

* {
  border: 0%;
  border-style: none;
  margin: 0%;
}

.header {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.header-top {
  align-items: center;
  background-color: rgb(67, 163, 58);
  display: flex;
  flex-direction: row;
  height: 56px;
  width: 100%;
}

.page-title {
  cursor: -webkit-grab;
  cursor: grab;
  font-size: 36px;
  font-weight: 900;
  left: 50%;
  margin-top: 20px;
  position: absolute;
  text-shadow: 1px 1px 1px rgb(41, 38, 38);
  text-transform: uppercase;
  transform: translateX(-50%);
}

.page-title::selection {
  background-color: transparent;
}

.img-header {
  display: absolute;
  height: 34px;
  margin-left: 10px;
}

.btn-search {
  background-color: transparent;
  height: 36px;
  margin-right: 10px;
  position: absolute;
  right: 0;
}

.inp-search {
  background-image: linear-gradient(rgb(67, 163, 58), rgb(60, 179, 49));
  box-shadow: 1px 1px 7px rgb(29, 28, 29) inset;
  display: flex;
  height: 80px;
  justify-content: space-between;
  left: 0%;
  padding-left: 10%;
  padding-right: 10%;
  width: 100%;
}

.inp-txt-search {
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
  font-size: 20px;
  height: 60%;
  transition: width 0.4s ease-in-out;
  width: 60%;
}

.inp-txt-search:focus {
  height: 60%;
  width: 100%;
}

.btn-search-submit {
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  font-size: 14px;
  height: 60%;
  min-width: 80px;
}

.btn-search-submit:hover {
  box-shadow: 0.5px 0.5px 4px hsla(0, 21%, 36%, 0.365) inset;
}

.inp-group-search {
  align-items: center;
  display: flex;
  flex-direction: row;
  font-size: 16px;
  justify-content: space-between;
  margin-right: 10px;
  width: 50%;
}

.inp-group-search input {
  margin-top: -1px;
  vertical-align: middle;
}

.group-submit-txt {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 100%;
}

@media ( max-width : 786.98px ) {

  .inp-group-search {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    font-size: 13px;
    justify-content: flex-start;
    justify-items: center;
    margin-left: 4px;
    margin-top: 4px;
    padding: 0;
  }

  .btn-search-submit {
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    font-size: 12px;
    height: 60%;
    min-width: 60px;
  }

  .inp-search {
    padding: 0;
  }

  .page-title {
    font-size: 22px;
    margin-top: 16px;
    text-align: center;
    width: 80%;
  }

  .img-header {
    margin-left: 3px;
  }

  .btn-search {
    margin-right: 3px;
  }
}

.footer {
  background-color: rgb(67, 163, 58);
  bottom: 0;
  display: flex;
  position: fixed;
  width: 100%;
}

.footer ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
  margin: auto;
  padding: 0.5em;
  width: 100%;
}

.footer ul li button {
  background-color: transparent;
  border: none;
}

.profile-page {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.email-profile-page {
  bottom: 70%;
  font-size: 250%;
  position: absolute;
  text-shadow: 0.5px 0.5px 0.5px rgb(41, 38, 38);
  transform: translateY(-30%);
}

.btn-profile-page {
  bottom: 30%;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  height: 30%;
  justify-content: space-between;
  position: absolute;
  width: 50%;
}

.btn-profile-page button {
  background-color: transparent;
  border-bottom: 1px solid rgba(0, 0, 0, 0.568);
  box-shadow: 1px 1px 7px rgb(29, 28, 29);
  font-size: 16px;
  font-weight: 900;
  height: 270%;
  opacity: 0.6;
  text-shadow: 0.5px 0.5px 0.5px rgb(41, 38, 38);
  text-transform: uppercase;
  transition: 0.4s;
  width: 100%;
}

.btn-profile-page button:hover {
  box-shadow: 1px 1px 7px rgb(29, 28, 29) inset;
}

@media ( max-width : 786.98px ) {

  .email-profile-page {
    font-size: 140%;
  }

  .btn-profile-page {
    width: 80%;
  }

  .btn-profile-page button {
    font-size: 14px;
  }
}

@media only screen and ( max-width : 768px ) {

  .path-buttons-container {
    align-items: center;
    background-color: #48487d;
    display: flex;
    flex-direction: column;
    height: 85vh;
    justify-content: center;
    width: 100%;
  }

  .path-btn {
    background-color: #c9b34d;
    border: black 1px solid;
    border-radius: 60px;
    color: #636396;
    font-size: 25px;
    font-weight: 900;
    height: 80px;
    margin: 20px;
    text-align: center;
    text-shadow: 1px 1px 1px rgb(41, 38, 38);
    width: 250px;
  }

  .path-btn:active {
    background-color: white;
    border: black 1px solid;
    border-radius: 60px;
    color: #636396;
    font-size: 25px;
    height: 80px;
    margin: 20px;
    text-align: center;
    width: 250px;
  }

  .area-select {
    background-color: rgb(67, 163, 58);
    border: rgb(67, 163, 58) solid 3px;
    box-shadow: 1px 1px 7px rgb(29, 28, 29) inset;
    color: whitesmoke;
    font-size: 18px;
    height: 40px;
    margin: 0;
    text-align-last: center;
    width: 100%;
  }
}

@media only screen and ( min-width : 768px ) {

  .path-buttons-container {
    align-items: center;
    background-color: #48487d;
    display: flex;
    flex-direction: column;
    height: 90vh;
    justify-content: center;
    width: 100%;
  }

  .path-btn {
    background-color: #c9b34d;
    border-radius: 60px;
    box-shadow: 2px 2px 2px rgb(29, 28, 29);
    color: #636396;
    font-size: 28px;
    font-weight: 900;
    height: 120px;
    margin: 50px;
    text-align: center;
    text-shadow: 1px 1px 1px rgb(41, 38, 38);
    width: 500px;
  }

  .path-btn:hover {
    background-color: #c9b24d96;
    border-radius: 60px;
    box-shadow: 2px 2px 8px rgb(29, 28, 29) inset;
    color: #636396;
    font-size: 28px;
    height: 120px;
    margin: 50px;
    opacity: 80%;
    text-align: center;
    transition: 0.4s;
    width: 500px;
  }

  .path-btn:active {
    background-color: white;
    border: black 1px solid;
    border-radius: 60px;
    color: #636396;
    font-size: 28px;
    height: 120px;
    margin: 50px;
    text-align: center;
    width: 500px;
  }

  .area-select {
    background-color: rgb(67, 163, 58);
    border: rgb(67, 163, 58) solid 3px;
    box-shadow: 1px 1px 7px rgb(29, 28, 29) inset;
    color: whitesmoke;
    font-size: 25px;
    height: 50px;
    margin: 0;
    text-align-last: center;
    width: 100%;
  }
}

.cards-container {
  background-color: #48487d;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
  min-height: 100vh;
  padding-bottom: 50px;
  width: 100%;
}

.card {
  align-items: center;
  background-color: #c9b24dd2;
  display: flex;
  flex-direction: column;
  height: 410px;
  margin: 15px;
  width: 360px;
}

.card:hover {
  background-color: #f1f1f156;
  box-shadow: 1px 1px 7px rgb(29, 28, 29) inset;
}

.card-img {
  box-shadow: 0.3px 0.3px 3px rgb(29, 28, 29);
  margin: 30px 10px 10px;
  min-width: 300px;
  width: 80%;
}

.card-img:hover {
  margin: 5px;
  position: absolute;
  transition: 0.4s;
  width: 95%;
}

.card-title {
  color: #48487d;
  font-size: 180%;
  font-weight: 800;
  height: 60px;
  margin: 10px;
  margin-top: 350px;
  max-height: 60px;
  overflow: hidden;
  position: absolute;
  text-shadow: 1px 1px 1px rgb(41, 38, 38);
  width: 100%;
}

/* @media only screen and ( min-width : 768px ) {

} */

@media only screen and ( max-width : 768px ) {

  .card {
    align-items: center;
    background-color: #c9b24dc2;
    display: flex;
    flex-direction: column;
    height: 180px;
    margin: 8px;
    width: 150px;
  }

  .card:hover {
    background-color: #f1f1f156;
    box-shadow: 1px 1px 7px rgb(29, 28, 29) inset;
  }

  .card-img {
    margin: 20px 5px 5px;
    min-width: 80px;
    width: 80%;
  }

  .card-title {
    color: #48487d;
    font-size: 13px;
    font-weight: 800;
    margin-top: 99%;
    max-height: 34px;
    position: absolute;
    text-shadow: 0.3px 0.3px 1px rgb(41, 38, 38);
  }

}

.input-group-filter {
  display: flex;
  justify-content: space-around;
}

.input-group-filter input {
  opacity: 0;
  position: fixed;
}

.input-group-filter label {
  align-items: center;
  box-shadow: 0.6px 0.6px 4px rgb(29, 28, 29);
  display: flex;
  height: 40px;
  justify-content: center;
  line-height: 0;
  margin-top: 4px;
  text-align: center;
  width: 10%;
}

.input-group-filter label:hover {
  border: none;
  border-radius: 2%;
  box-shadow: 0.3px 0.3px 3px rgb(29, 28, 29) inset;
}

@media ( max-width : 786.98px ) {

  .input-group-filter label {
    height: 35px;
    text-align: center;
    width: 32%;
  }

  .input-group-filter {
    flex-flow: wrap;
    flex-direction: row;
  }
}

.recipe_details {
  list-style: none;
  margin: 0 0 120px;
}

.startBtn {
  border: 1px solid black;
  bottom: 0;
  height: 50px;
  position: fixed;
  width: 100%;
}

.image {
  height: 100px;
}

.error404 {
  background-image: url(/recipes-app/static/media/DEFINICIONES.-ERROR-404.1e5ba0cc.png);
  height: 420px;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  width: 670px;
}

@media ( max-width : 786.98px ) {

  .error404 {
    background-image: url(/recipes-app/static/media/DEFINICIONES.-ERROR-404.1e5ba0cc.png);
    height: 420px;
    left: 28%;
    object-fit: cover;
    position: absolute;
    right: auto;
    transform: translateX(-50%);
    width: 520px;
  }
}


